          <div class="detail-view__content">
            <h2 class="detail-view__title | title title--large">
              Woods & forests
            </h2>
            <p>
              In recent years people have realized the importance of proper diet
              and exercise, and recent surveys show that over the last 20 years
              people are eating better and working out more often, resulting in
              people living longer, but people are still lacking in their
              understanding that their mental well being is just as important as
              their physical health.
            </p>
            <p>
              Today most people get on average 4 to 6 hours of exercise every
              day, and make sure that everything they put in their mouths is not
              filled with sugars or preservatives, but they pay no attention to
              their mental health, no vacations, not even the occasional long
              weekend, 60 hour weeks, taking work home with them and even
              working weekends. All of this for hopes of one day getting that
              big promotion. What good will it do you when your brain overloads
              and you have a breakdown in the office.
            </p>
            <p>
              In the end your physical health will suffer no matter how well you
              eat and how often you exercise. You will wind up with high blood
              pressure, stress and tension all of which raises the chances of
              you having a stroke or heart attack. In hopes of helping you avoid
              this I am providing you with the things I do to keep my mental
              health in tip top condition.
            </p>
